
* {
  transition: all 0.3s;
}
html {
  min-height: 100vh;
}
html #hello {
  font-family: 'Avenir', Helvetica, Arial, sans-serif, "Microsoft Yahei";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html #hello .skip {
  background: #534847;
  border-radius: 5px;
  color: #fff;
  display: inline-block;
  padding: 10px;
  position: fixed;
  right: 20px;
  bottom: 15px;
  z-index: 1;
}
html #hello .skip:hover {
  opacity: 0.5;
}
html #hello .skip a {
  color: #fff;
  text-decoration: none;
}


