
.clearfix:after {
  content: '';
  clear: both;
  display: block;
}
.contact {
  background: #d1b6e1;
}
.pic-wrapper {
  padding-top: 120px;
  width: 100%;
  height: 100vh;
  padding-bottom: 30px;
  border-bottom: 1px solid #008000;
}
.pic-wrapper .pic {
  width: 50%;
  float: left;
  text-align: center;
}
.pic-wrapper .pic img {
  width: 50%;
}
.pic-wrapper .pic p {
  font-size: 30px;
  margin-top: 30px;
  font-weight: 600;
}


