
.info {
  position: relative;
  background: url("./background.jpg");
  background-position: center;
  background-size: cover;
  color: #fff;
  line-height: 20px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif, "Microsoft Yahei";
}
.info .info-bg {
  box-sizing: border-box;
  padding-top: 50px;
  padding-left: 70px;
  overflow: hidden;
  height: 100vh;
  background: rgba(0,0,0,0.6);
  text-overflow: ellipsis;
}
.info .info-bg .title {
  color: #d1b6e1;
  margin-bottom: 30px;
}
.info .info-bg .basic {
  margin-bottom: 40px;
}
.info .info-bg .basic li {
  margin-bottom: 10px;
}
.info .info-bg .education {
  position: absolute;
  right: 280px;
  top: 100px;
}
.info .info-bg .education p {
  margin-top: 10px;
}
.info .info-bg .desc {
  margin-top: 350px;
  letter-spacing: 2px;
}
@media only screen and (max-width: 1280px) {
.info .info-bg .desc {
    line-height: 25px;
}
}


