
a {
  text-decoration: none;
}
#nav {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  width: 250px;
  background: linear-gradient(135deg, #2c2e2c 0, #202021 100%);
}
#nav .header {
  margin: 30px auto 30px auto;
  width: 50%;
  text-align: center;
}
#nav .header .name {
  margin-top: 40px;
  font-size: 40px;
}
#nav .header .name a.active {
  color: #ae3a3b;
}
#nav .header .name a:hover {
  color: #30a9de;
}
#nav .nav-container {
  margin-top: 80px;
}
#nav .nav-container li {
  margin-bottom: 50px;
  text-align: center;
}
#nav .nav-container li a {
  color: #fff;
  font-size: 20px;
  transition: all 0.3s;
}
#nav .nav-container li a.active {
  color: #efdc05;
}
#nav .nav-container li a:hover {
  color: #2bb0d4;
}
#nav #footer {
  position: absolute;
  bottom: 30px;
  text-align: center;
}
#nav #footer .dream {
  color: #a3a1a1;
  margin: 10px 5px;
  font-size: 13px;
}
#nav #footer .dream .name {
  font-weight: 700;
}
#nav #footer .logo {
  font-size: 14px;
}
#nav #footer .logo a {
  display: inline-block;
  margin: 5px;
  line-height: 40px;
  text-align: center;
  width: 40px;
  height: 40px;
  color: #fff;
  font-size: 24px;
  border-radius: 50%;
}
#nav #footer .logo a:hover {
  opacity: 0.5;
}
#nav #footer .logo .blog {
  background-color: #584613;
}
#nav #footer .logo .github {
  background-color: #00ab6c;
}


