
.clearfix:after {
  content: '';
  clear: both;
  display: block;
}
ul {
  list-style: none;
}
.work {
  height: 100vh;
  background-image: url("./bg-work.jpg");
  background-size: cover;
  background-position: center;
  color: #fff;
  font-size: 16px;
  line-height: 25px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif, "Microsoft Yahei";
}
.work h1 {
  margin-bottom: 30px;
  color: #d1b6e1;
}
.work .gap {
  margin-bottom: 10px;
}
.work .company {
  float: left;
  width: 25vw;
  padding-top: 40px;
  padding-left: 40px;
}
.work .items {
  float: right;
  width: 25vw;
  padding-top: 40px;
  padding-right: 40px;
}
.work .items .project-internet {
  font-weight: 600;
}
.work .items .project-sanbao {
  font-weight: 600;
}


